/*
 * main app javascript
 * @TODO: migrate to Typescript
 */
;(function($, window, document, undefined) {
  'use strict';

  var $ripple = $('.btn__ripple');

  $ripple.on('click', function(e) {

    var $this = $(this);
    var $offset = $this.parent().offset();
    var $circle = document.createElement( "span" );
    $circle.className = 'btn-circle';
    $circle = $($circle);
    $this.append($circle);

    var x = e.pageX - $offset.left;
    var y = e.pageY - $offset.top;

    $circle.css({
      top: y + 'px',
      left: x + 'px'
    });
    $circle.addClass('is-active');

  });

  $ripple.on('animationend webkitAnimationEnd oanimationend MSAnimationEnd', function(e) {
  	$(this).find('is-active').removeClass('is-active');
  });

})(jQuery, window, document);

/*
 * calling function to initiate plugins on DOM ready
*/
function initPlugins() {
  // initiate popover and tooltip
  console.log('app init');
  $('[data-bs-toggle="popover"]').popover();
  $('[data-bs-toggle="tooltip"]').tooltip();
  const sidebarWrapper = document.getElementById('sidebar-wrapper');
  let scrollToTopVisible = false;
  // Closes the sidebar menu
  const menuToggle = document.body.querySelector('.menu-toggle');
  menuToggle.addEventListener('click', event => {
      event.preventDefault();
      sidebarWrapper.classList.toggle('active');
      _toggleMenuIcon();
      menuToggle.classList.toggle('active');
  })

  // Closes responsive menu when a scroll trigger link is clicked
  var scrollTriggerList = [].slice.call(document.querySelectorAll('#sidebar-wrapper .js-scroll-trigger'));
  scrollTriggerList.map(scrollTrigger => {
      scrollTrigger.addEventListener('click', () => {
          sidebarWrapper.classList.remove('active');
          menuToggle.classList.remove('active');
          _toggleMenuIcon();
      })
  });

  function _toggleMenuIcon() {
      const menuToggleBars = document.body.querySelector('.menu-toggle > .fa-bars');
      const menuToggleTimes = document.body.querySelector('.menu-toggle > .fa-xmark');
      if (menuToggleBars) {
          menuToggleBars.classList.remove('fa-bars');
          menuToggleBars.classList.add('fa-xmark');
      }
      if (menuToggleTimes) {
          menuToggleTimes.classList.remove('fa-xmark');
          menuToggleTimes.classList.add('fa-bars');
      }
  }

  // Scroll to top button appear
  document.addEventListener('scroll', () => {
      const scrollToTop = document.body.querySelector('.scroll-to-top');
      if (document.documentElement.scrollTop > 100) {
          if (!scrollToTopVisible) {
              fadeIn(scrollToTop);
              scrollToTopVisible = true;
          }
      } else {
          if (scrollToTopVisible) {
              fadeOut(scrollToTop);
              scrollToTopVisible = false;
          }
      }
  });
}
$(window).on( "load", function() {
  initPlugins();
  timer();
});

function fadeOut(el) {
  el.style.opacity = 1;
  (function fade() {
      if ((el.style.opacity -= .1) < 0) {
          el.style.display = "none";
      } else {
          requestAnimationFrame(fade);
      }
  })();
}

function fadeIn(el, display) {
  el.style.opacity = 0;
  el.style.display = display || "block";
  (function fade() {
      var val = parseFloat(el.style.opacity);
      if (!((val += .1) > 1)) {
          el.style.opacity = val;
          requestAnimationFrame(fade);
      }
  })();
}

function timer() {
  // Set the date we're counting down to
var countDownDate = new Date("Oct 22, 2022 20:45:00").getTime();

// Update the count down every 1 second
var x = setInterval(function() {

  // Get today's date and time
  var now = new Date().getTime();

  // Find the distance between now and the count down date
  var distance = countDownDate - now;

  // Time calculations for days, hours, minutes and seconds
  var days = "<div class='gradient-time'>" + Math.floor(distance / (1000 * 60 * 60 * 24)) + "d</div>";
  var hours = "<div class='gradient-time'>" + Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)) + "h</div>";
  var minutes = "<div class='gradient-time'>" + Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60)) + "m</div>";
  var seconds = "<div class='gradient-time'>" + Math.floor((distance % (1000 * 60)) / 1000) + "s</div>";

  // Display the result in the element with id="demo"
  document.getElementById("timer").innerHTML = days + hours + minutes + seconds;

  // If the count down is finished, write some text
  if (distance < 0) {
    clearInterval(x);
    document.getElementById("timer").innerHTML = "EXPIRED";
  }
}, 1000);
}